body {
  margin: 0;
  padding: 0;
  font-family: 'poppins', sans-serif;
  height: 100px;
  
}

.banner {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.banner video {
  object-fit: cover;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}
.banner .content {
  position: relative;
  z-index: 1;
  max-width: 100px;
  margin: 0 auto;
  text-align: center;
}

.banner .content h1 {
  margin-bottom: 50px;
  float: right;
  padding: 0;
  font-size: 4.5rem;
  text-transform: uppercase;
  color: #fff;
}

.chatPannel {
  text-align: center;
}

